$primary-color: #060b35;
$secondary-color: #00acdc;

$padding-catalog-x: 40px;
$padding-catalog-y: 50px;
$margin-right-card: 48px;
$card-width: 200px;
$count-card: 4;
$catalog-width: $card-width * $count-card
  + $margin-right-card * ($count-card - 1)
  + 2 * $padding-catalog-x;
$text-card-color: #616070;
